  .preptrator {
    display: flex;
    flex-direction: column;
    align-items: flex;
    width:80%;
    height:100%;
  }
  
  .group {
    display: flex;
    flex-direction: row;
    margin: 6px;
  }
  
  .block {
    width: 62px;
    height: 62px;
    margin: 6px;
    background-color: none;
  }


/* photos */

.photo-section {
    padding:30px 0px;
    width:100%;
    margin:auto;
    display:flex;
    max-width:1200px;
}

.photo {
    width:100%;
    padding-right:15px;
}
.photo img {
    width:100%;	
}

p.caption {
    font-size:0.9rem;
    margin:0;
}



/* mobile */

@media (max-width:700px) {

    .chart-row {
        display:block;
    }

    .chart-row .chart {
        padding:0;
    }

    .photo-section {
        display:block;
    }

    .photo {
        padding:0;
    }

}
